.projects {
  // background-color: var(--clr-black--2);
  padding: 5rem 0;

  &__content {
    // background-color: var(--clr-black--1);
    padding: 5rem 2.5rem;
    border-radius: 2rem;
    width: 100%;

    @include responsive(400) {
      padding-left: 0;
      padding-right: 0;
      border-radius: 0;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;

    @include responsive(1200) {
      grid-template-columns: 1fr 1fr;
      gap: 8rem;
    }

    @include responsive(600) {
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    // gap: 3rem;
    // min-height: 42.5rem;
    border-radius: 1.5rem;
    // padding: 3rem;
    // background-color: var(--clr-black--2);
    height: 100%;

    @include responsive(400) {
      border-radius: 0;
    }
  }
}

.project {
  &__icon-box {
    width: 25%;

    &::after {
      content: '';
      display: block;
      border-bottom: 3px solid var(--clr-purple);
    }
  }

  &__description {
    flex-grow: 1;
  }
}

